// Подключение свайпера
import Swiper, { Navigation, Keyboard } from 'swiper';

Swiper.use([Navigation, Keyboard]);

const swiper = new Swiper('.ourAssistance__sliders', {
  slidesPerView: 3,
  spaceBetween: 50,
  grabCursor: true,
  speed: 500,

  keyboard: {
    enabled: true,
    onlyInViewport: true,
    pageUpDown: true,
  },

  navigation: {
    nextEl: '.ourAssistance__slider-btn--next',
    prevEl: '.ourAssistance__slider-btn--prev',
  },
});
