document.addEventListener('DOMContentLoaded', () => {
  // Анимация ticker с помощью библиотеки GSAP (Greensock)
  gsap.registerPlugin(ScrollTrigger); // Подключения плагина ScrollTrigger

  const scrollObj = {
    trigger: '.ticker',
    start: '-400px bottom',
    scrub: 1,
  };

  gsap.to('.marquee', {
    scrollTrigger: scrollObj,
    xPercent: -100,
    duration: 20,
    ease: 'none',
  });

  gsap.from('.reverce', {
    scrollTrigger: scrollObj,
    xPercent: -100,
    duration: 20,
    ease: 'none',
  });
});
