const outNum = (n, num) => {
  const l = document.querySelector('#numberCounter');
  const step = 1;

  function randomInt() {
    n += step;

    let interval = setTimeout(() => {
      l.innerHTML = n.toLocaleString('ru');

      randomInt();
    }, Math.round(Math.random() * (1000 - 100 + 1)) + 100);

    if (n == num) {
      clearTimeout(interval);
    }
  }

  randomInt();
};

outNum(50000, 100000000);
